@import "./StyleAuth";
@import "./StyleCommon";

@mixin Container {
  width: 85%;
  height: 85%;
  background: #FEFEFF;
  border-radius: 8px;
  margin-top: 3%;
  @include displayFlex(row, "", center);
}

@mixin Slider {
  background: #ECECF9;
  border-radius: 8px 0 0 8px;
  height: 100%;
  width: 22%;

  //@media screen and (min-width: 960px) {
  //  width: 22.5%;
  //  height: 100%;
  //}
  //@media screen and (min-height: 560px) and (max-height: 700px) {
  //  width: 18%;
  //  height: 100%;
  //}
}

@mixin SliderHeader {
  width: 100%;
  min-height: 30%;
  background: #D9D9F1;
  border-radius: 8px 0 0 0;
  padding: 10px 0;
}

@mixin NumberCards {
  @include displayFlex(column, "", center);

  .numberTitle {
    @include h4();
    margin-top: 20px;
    text-align: center;
    @media screen and (max-width: 1030px) {
      @include h5();
      margin-top: 20px;
    }
  }

  .doubleRange {
    width: 81%;
    height: 5px;
    opacity: 0.5;
    border-radius: 10px;
    margin-top: 40px;
  }
}

@mixin MainButton {
  @include Button();
  @include displayFlex(row, center, center);
  padding: 3px;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  text-transform: none;
  border: 1px solid #635D80;
  border-radius: 8px;
  background: none;
  color: #0c224f;

  &:hover {
    background-color: #d9d9f1;
    color: #1b4db3;
  }
}

@mixin MainButtonActive {
  @include MainButton();
  background-color: #d9d9f1;
  box-shadow: 0 15px 20px rgba(31, 78, 169, 0.4);
  color: #1b4db3;
  transform: translateY(-7px);

}

@mixin NavBtnActive {
  @include displayFlex(row,center,center);
  @include h6();
  cursor: pointer;
  margin-bottom: 0;
  text-align: center;
  width: 346px;
  height: 100%;
  background: #DCCCDB;
  border: none;
  border-bottom: 2px solid #21268F;
  text-decoration: none;

  &:hover{
    color: $ColorPrimary;
  }
}

@mixin NavBtnInactive {
  @include displayFlex(row,center,center);
  @include h6();
  cursor: pointer;
  margin-bottom: 0;
  text-align: center;
  width: 346px;
  height: 100%;
  background: #EBE0E9;
  border: none;

  &:hover{
    color: $ColorPrimary;
  }
}
